
.titlebar {
  background: #0d968a;
  padding: 2em;
  color: #fff;
}
.branding {
  text-align: center;
}
.branding-title,
.branding-tagline {
  margin: 0;
}
.branding-title {
  font-size: 1.75em;
  text-transform: uppercase;
}
.branding-tagline {
  font-size: .875em;
  text-transform: uppercase;
}



