
.content {
  height: 100%;
}
.container {
  padding: 2.5em;
}
.search-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-bottom: 2em;
}
@media (min-width: 50em) {
.search-container {
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
}
.input-field {
  margin: 0 0 .5em;
}
@media (max-width: 50em) {
.input-field {
    width: 100%;
}
.input-field:nth-child(2) {
    padding-right: .25em;
    width: 50%;
}
.input-field:nth-child(3) {
    padding-left: .25em;
    width: 50%;
}
}
@media (min-width: 50em) {
.input-field {
    margin: 0 .5em;
}
}
.input-label {
  display: block;
  font-weight: 700;
  margin-bottom: .15em;
}
.input {
  border: .0625em solid #0d968a;
  padding: .5em 1em;
  width: 100%;
}
.input-button {
  -ms-flex-item-align: end;
      align-self: flex-end;
  border: 0;
  background: #0d968a;
  color: #fff;
  font-weight: 700;
  text-transform: uppercase;
  padding: .65em 2em;
}
.input-button:hover,
.input-button:focus {
  background: #0b736a;
}
@media (max-width: 50em) {
.input-button {
    width: 100%;
}
}
.chart-container {
  border: .0625em solid rgba(0, 0, 0, .12);
  box-shadow: 0 0 1em 0 rgba(6, 56, 51, .12);
  padding: .75em;
}
.chart-title {
  display: inline-block;
  font-size: 1.5em;
  text-transform: uppercase;
  vertical-align: baseline;
  margin: 0 .5em 1em 0;
}



